.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .house {
    text-align: center;
    width: 500px;
    height: 550px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 30px;
    float: left;

    .photo {
      text-align: center;

      img {
        width: 400px;
        height: 350px;
      }
    }

    .back a {
      cursor: pointer;
      color: black;
    }
  }

}